<template>
    <div class="page">
        <MyHeader>
            <div>消息中心</div>
        </MyHeader>
        <div class="content">
            <ul class="msg-list">
                <li class="icon-h">
                    <div>
                        <span>活动邀请</span>
                        <p>乘机必备7件套</p>
                        <i>今天 14：43</i>
                    </div>
                </li>
                <li class="icon-x">
                    <div>
                        <span>系统消息</span>
                        <p>暂无消息</p>
                    </div>
                </li>
                <li class="icon-j">
                    <div>
                        <span>交易提醒</span>
                        <p>暂无消息</p>
                    </div>
                </li>
                <li class="icon-w">
                    <div>
                        <span>我的关注</span>
                        <p>暂无消息</p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import "lib-flexible"
import MyHeader from "./header";
export default {
  name:"Message",
  data:function(){
      return {
          title:"message"
      }
  },
   components:{MyHeader}
}
</script>

<style scoped>
.page{
    width:100%;
    height: auto;
    overflow: hidden;
    background: #f6f6f6;
}
.content{
    height: auto;
    overflow: hidden;
    background: #fff;
}
.msg-list{
    height: auto;
    overflow: hidden;
}
.msg-list li{
    height: auto;
    overflow: hidden;
    padding: 30px 0 0 135px;
    background: url(./images/icon_list.png) no-repeat center;
}
.msg-list li.icon-h{
    background-size: 80px;
    background-position: 30px 40px;
}
.msg-list li.icon-x{
    background-size: 80px;
    background-position: 30px -100px;
}
.msg-list li.icon-j{
    background-size: 80px;
    background-position: 30px -250px;
}
.msg-list li.icon-w{
    background-size: 80px;
    background-position: 30px -400px;
}
.msg-list li div{
    position: relative;
    height: auto;
    overflow: hidden;
    border-bottom: 1px solid #d4d4d5;
}
.msg-list li div span{
    float: left;
    font-size: 32px;
    color: #000;
    clear: both;
}
.msg-list li div p{
    font-size: 30px;
    color: #a8a8a8;
    padding-top:20px;
    padding-bottom: 30px;
    clear: both;
}
.msg-list li div i{
    position: absolute;
    font-size: 24px;
    top: 0;
    right:30px;
    color: #a8a8a8;
    font-style: normal;
}
</style>




