<template>
    <div class="header">
        <div class="h-left"><slot name="left"><span @click="goBack">返回</span></slot></div>
        <div class="title"><slot></slot></div>
        <div class="h-right"><slot name="right"></slot></div>
    </div>
</template>

<script>
export default {
    name:"header",
    methods:{
        goBack:function(){
            history.back()
        }
    }
}
</script>

<style scoped>
.header{
    padding: 20px 15px;
    line-height: 50px;
    background: url(./images/header_bg.jpg) no-repeat;
    background-size: 100% 100%;
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    color: #fff;
    
}
.title{
    height: 50px;
    font-size: 34px;
}
.h-left,
.h-right{
    height: 50px;
    min-width: 90px;
}
.h-left span{
    font-size: 32px;
    float: left;
    display: inline-block;
    background: url(./images/icon_arrow_left.png) no-repeat left center;
    background-size: 24px;
    padding-left: 30px;
}
</style>



